@import '../../styles/shared.scss';

// 404 page container
.container {
  text-align: center;
}

// Image container
.thumb {
  margin: 0 auto;

  max-width: 200px;

  @media screen and (min-width: $mobile) {
    max-width: 320px;
  }
}

// Main image on page
.image {
  max-width: 100%;
  margin-bottom: 15px;

  animation: spin 12s infinite linear;
}

// Error description
.text {
  margin-bottom: 32px;
}

// Home button
.button {
  padding: 10px;

  border: 0px solid transparent;
  border-radius: 4px;

  text-decoration: none;

  color: $clear-color;
  background-color: $main-color;

  box-shadow: $main-shadow;

  opacity: 1;
  transition: opacity $transition-duration $timing-function;

  &:hover,
  &:focus {
    opacity: 0.8;

    transition: opacity $transition-duration $timing-function;
  }

  &:disabled {
    background-color: $inactive-color;
    cursor: auto;
  }
}

